.nav {
  position: relative;

}

.nav__logo {
  position: absolute;
  bottom: 16px;
  left: 20px;
  display: block;
  height: 40px;
}

.nav__logo-pink-white-desktop,
.nav__logo-pink-white-mobile,
.nav__logo-pink-white-tablet {
  fill: #ffffff;
}

.nav__logo-pink-white-mobile:hover {
  fill: @purp;
}

.nav__logo-pink-white-tablet:hover {
  fill: @purp;
}

.nav__logo-pink-white-desktop:hover {
  fill: @purp;
}

.nav__logo-pink-white-tablet {
  display: none;
}

.nav__logo-pink-white-desktop {
  display: none;
}
@media (min-width: @tab-width) {
  .nav__logo-pink-white-mobile {
    display: none;
  }

  .nav__logo-pink-white-tablet {
    display: block;
  }

  .nav__logo {
    position: absolute;
    bottom: 38px;
    left: 20px;
    display: block;
    height: 40px;
  }

  .nav {
    min-height: 110px;
  }
}
@media (min-width: @desk-width) {
  .nav__logo-pink-white-desktop {
    display: block;
  }

  .nav__logo-pink-white-tablet {
    display: none;
  }
  .nav-logo {
    margin-left: 130px;
    height: 40px;
    margin-bottom: 20px;
  }
}
