@font-face {
  font-family: 'OpenSans';
  font-style: normal;
  font-weight: normal;
  src: local(''), url('../fonts/opensans.woff2') format('woff2'), url('../fonts/opensans.woff') format('woff');
}
@font-face {
  font-family: 'OpenSansBold';
  font-style: normal;
  font-weight: bold;
  src: local(''), url('../fonts/opensansbold.woff2') format('woff2'), url('../fonts/opensansbold.woff') format('woff');
}
@font-face {
  font-family: 'OpenSansLight';
  font-style: normal;
  font-weight: normal;
  src: local(''), url('../fonts/opensanslight.woff2') format('woff2'), url('../fonts/opensanslight.woff') format('woff');
}
html,
body {
  position: relative;
  padding: 0;
  margin: 0;
  text-align: center;
  font-family: OpenSans, sans-serif;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
.visually-hidden {
  overflow: hidden;
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
}
img {
  max-width: 100%;
  height: auto;
}
.page__footer {
  min-height: 284px;
}
@media (min-width: 660px) {
  .page__footer {
    min-height: 127px;
    padding: 0 20px;
  }
}
@media (min-width: 1200px) {
  .page__footer {
    min-height: 197px;
    padding: 0 80px;
  }
}
.header {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 66px;
  background: rgba(0, 0, 0, 0.3);
}
.header--nav-opened {
  background: #283645;
  min-height: 66px;
}
.header__nav {
  height: 66px;
  width: 100%;
  display: flex;
  flex-direction: column;
}
@media (min-width: 660px) {
  .header--nav-opened {
    min-height: 110px;
  }
  .header {
    min-height: 110px;
  }
}
@media (min-width: 1200px) {
  .header {
    flex-direction: row;
    background: rgba(0, 0, 0, 0.3);
  }
}
.nav-list {
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  display: none;
  margin-top: 0px;
  height: 244px;
}
.header--nav-opened .nav-list {
  display: block;
}
.nav-list__link {
  color: #ffffff;
  font-family: 'OpenSansBold';
  font-size: 18px;
  line-height: 18px;
  display: block;
}
.nav-list__link:hover {
  color: #D22856;
}
.nav-list__link:active {
  opacity: 0.3;
}
.nav-list__link:visited {
  outline: 1px;
}
.nav-list__item {
  min-height: 41px;
  border-bottom: 1px solid #000000;
  box-shadow: 0px 1px 0px, inset 0px 1px 0px rgba(255, 255, 255, 0.15);
  padding-top: 23px;
}
@media (min-width: 1200px) {
  .nav-list,
  .header--nav-opened .nav-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 48px;
    height: 57px;
    margin-right: 130px;
    margin-bottom: 0px;
  }
  .nav-list__item {
    padding-top: 0;
    border: none;
    box-shadow: none;
    margin-right: 78px;
  }
  .nav-list__item:last-child {
    width: 131px;
    margin-right: 0;
  }
  .nav-list__link {
    font-family: 'OpenSans', Arial, sans-serif;
  }
}
.nav {
  position: relative;
}
.nav__logo {
  position: absolute;
  bottom: 16px;
  left: 20px;
  display: block;
  height: 40px;
}
.nav__logo-pink-white-desktop,
.nav__logo-pink-white-mobile,
.nav__logo-pink-white-tablet {
  fill: #ffffff;
}
.nav__logo-pink-white-mobile:hover {
  fill: #D22856;
}
.nav__logo-pink-white-tablet:hover {
  fill: #D22856;
}
.nav__logo-pink-white-desktop:hover {
  fill: #D22856;
}
.nav__logo-pink-white-tablet {
  display: none;
}
.nav__logo-pink-white-desktop {
  display: none;
}
@media (min-width: 660px) {
  .nav__logo-pink-white-mobile {
    display: none;
  }
  .nav__logo-pink-white-tablet {
    display: block;
  }
  .nav__logo {
    position: absolute;
    bottom: 38px;
    left: 20px;
    display: block;
    height: 40px;
  }
  .nav {
    min-height: 110px;
  }
}
@media (min-width: 1200px) {
  .nav__logo-pink-white-desktop {
    display: block;
  }
  .nav__logo-pink-white-tablet {
    display: none;
  }
  .nav-logo {
    margin-left: 130px;
    height: 40px;
    margin-bottom: 20px;
  }
}
