.menu-toggle {
  display: block;
  padding: 0;
  width: 50px;
  height: 24px;
  background: inherit;
  border: none;
  position: absolute;
  bottom: 19px;
  right: 20px;
  cursor: pointer;
}
.menu-toggle::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  width: 50px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}
.menu-toggle:hover::before {
  background-color: #D22856;
  box-shadow: 0 8px 0 0 #D22856, 0 16px 0 0 #D22856;
}
.menu-toggle:active::before {
  background-color: #D22856;
  box-shadow: 0 8px 0 0 #D22856, 0 16px 0 0 #D22856;
  opacity: 0.3;
}
.header--nav-opened .menu-toggle::after,
.header--nav-opened .menu-toggle::before {
  top: 10px;
  right: 0;
  content: "";
  position: absolute;
  width: 27px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: none;
}
.header--nav-opened .menu-toggle::after {
  transform: rotate(45deg);
}
.header--nav-opened .menu-toggle::before {
  transform: rotate(-45deg);
}
.header--nav-opened .menu-toggle:hover::after,
.header--nav-opened .menu-toggle:hover::before {
  background-color: #D22856;
}
.header--nav-opened .menu-toggle:active::after,
.header--nav-opened .menu-toggle:active::before {
  border: none;
  opacity: 0.3;
}
@media (min-width: 660px) {
  .menu-toggle {
    display: block;
    padding: 0;
    width: 50px;
    height: 24px;
    background: inherit;
    border: none;
    position: absolute;
    bottom: 38px;
    right: 20px;
    cursor: pointer;
  }
}
@media (min-width: 1200px) {
  .menu-toggle {
    display: none;
  }
}
